const selectAuthError = (code) => {
    switch (code) {
        case "auth/email-already-exists":
            return "Podany adres e-mail jest ju uywany przez istniejcego uytkownika. Kady uytkownik musi mie unikalny adres e-mail.";
        case "auth/internal-error":
            return "Serwer uwierzytelniania napotka nieoczekiwany bd podczas prby przetworzenia dania.";
        case "auth/invalid-display-name":
            return "Podana warto waciwoci uytkownika displayName jest nieprawidowa. Musi to by niepusty cig.";
        case "auth/invalid-email":
            return "Podana warto waciwoci uytkownika email jest nieprawidowa. Musi to by adres e-mail w postaci cigu znakw.";
        case "auth/user-not-found":
            return "Nie istnieje aden rekord uytkownika odpowiadajcy podanemu identyfikatorowi.";
        case "auth/wrong-password":
            return "Haslo jest nieprawidowe";
        case "auth/weak-password":
            return "Haslo jest zbyt sabe i nie spenia warunkow wymaganych do zalozenia konta.";
        case "auth/email-already-in-use":
            return "Istnieje juz konto z podanym emailem."
        default:
            return "Wystpi nieznany bld: " + code;
    }

}

export default {
    selectAuthError
};